<template>
  <div class="layout-wrapper" v-cloak>
    <transition name="fade">
      <div class="home-title" v-if="titleShow">
        <div class="title-page"><span>My</span><span>Blog</span></div>
        <div class="title-author">Dao Dao</div>
        <div class="title-text">Better an empty purse than an empty head</div>
      </div>
    </transition>
    <!-- <layout-container> -->
    <el-container class="page-layout">
      <el-aside :width="sideWiden?'30vw': fold?'4vw': '20vw'" :class="['aside-layout', sideWiden?'widened': 'narrowed']" v-if="sideShow">
        <transition name="fade">
          <!-- <i class="iconfont icon-back" @click="handleBack" v-if="!sideWiden"></i> -->
          <i class="iconfont icon-home" @click="handleHome" v-if="!sideWiden"></i>
        </transition>
        <transition name="fade">
          <div class="aside-list" v-if="!sideWiden">
            <div :class="['aside-list-item', $route.name ==='Blog' && 'is-selected']" @click="$router.push('/blog')">BLOG</div>
            <div :class="['aside-list-item', $route.name ==='Note' && 'is-selected']" @click="$router.push('/note')">NOTE</div>
            <div :class="['aside-list-item', $route.name ==='About' && 'is-selected']" @click="$router.push('/about')">ABOUT</div>
          </div>
        </transition>
      </el-aside>
      <!-- <layout-nav :is-show="sideShow" :widen="sideWiden" /> -->
      <el-main class="main-layout" v-if="mainShow">
        <router-view />
      </el-main>
    </el-container>
    <!-- </layout-container> -->
  </div>
</template>

<script>
// @ is an alias to /src
// import LayoutNav from "./component/LayoutNav.vue";
// import LayoutContainer from "./component/LayoutContainer.vue";

export default {
  name: "Layout",
  components: {
    // LayoutNav,
    // LayoutContainer
  },
  data() {
    return {
      mainShow: false,
      titleShow: false,
      sideShow: false,
      sideWiden: false,
      fold: false
    };
  },
  watch: {
    $route(to, from) {
      this.titleShow = this.$route.name === "Home";
      if ( this.$route.name === "Home" ) {
        this.sideWiden = true
      }
      console.log('to, from', to, from)
      if (to.name === "Home") {
        this.sideWiden = true
      } else {
        this.sideWiden = false
      }
    }
  },
  mounted() {
    console.log('$route',this.$router,this.$route)
    this.sideShow = true;
    setTimeout(()=> {
      this.mainShow = true;
    }, 600)
    this.titleShow = this.$route.name === "Home";
    if ( this.$route.name === "Home" ) {
      this.sideWiden = true
    }
    console.log('process', process)
  },
  methods: {
    handleBack() {
      this.$router.go(-1)
    },
    handleHome() {
      this.$router.push('/')
    }
  },
};
</script>
<style lang="scss" scoped>
  @import '@/assets/css/base.scss';
  .layout-wrapper {
    overflow: hidden;
    .page-layout {
      height: 100vh;
    }
    .home-title {
      z-index: 99;
      position: fixed;
      left: 16vw;
      top: 48vh;
      transform: translateY(-50%);
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .title-page span {
        font-size: 10vh;
        color: #BCFB25;
        text-shadow: #00b4aa .5vh .5vh 0;
        & + span {
          margin-left: 2vw;
          color: #fff;
        }
      }
      .title-author {
        font-size: 6vh;
        color: #fff;
        text-shadow: #00b4aa .5vh .5vh 0;
        margin-top: 1vh;
      }
      .title-text {
        position: relative;
        font-size: 3vh;
        color: #fff;
        text-shadow: #00b4aa .2vh .2vh 0;
        margin-top: 2vh;
        &::after {
          content: '';
          position: absolute;
          bottom: -.4vh;
          left: 0;
          height: 1px;
          width: 65%;
          background: #fff;
        }
      }
    }
    .aside-layout {
      position: relative;
      z-index: 1;
      padding: 2vh 2vw;
      background: linear-gradient(45deg, #75BAD2, #8ADBF8);
      overflow: hidden;
      &.widened {
        animation: widen 1s ease;
      }
      &.narrowed {
        animation: narrow 1s ease;
      }
      .aside-list {
        display: inline-flex;
        width: 90vh;
        color: #fff;
        text-align: center;
        transform: rotate(90deg);
        transform-origin: top left;
        &-item {
          flex: 1;
          cursor: pointer;
          &+ .aside-list-item {
            border-left: 1px solid #fff;
          }
          &.is-selected {
            background: #00b4aa;
          }
          &:hover {
            background: rgba($color: #fff, $alpha: .4);
          }
        }
      }
    }
    .main-layout {
      padding: 0;
    }
    .iconfont {
      position: absolute;
      color: #fff;
      font-size: 3vh;
      cursor: pointer;
      &:hover {
        opacity: .6;
      }
      &.icon-back {
        left: 2vw;
        top: 2vh;
      }
      &.icon-home {
        left: 2vw;
        bottom: 2vh;
      }
    }
  }


.fade-enter-active {
  transition: all 1s ease;
}
.fade-leave-active {
  transition: all .5s ease;
}
.fade-enter, .fade-leave-to {
  transform: translateX(10vw);
  opacity: 0;
}

@keyframes widen {
  from {
    width: 20vw;
  }
  to {
    width: 30vw;
  }
}
@keyframes narrow {
  from {
    width: 30vw;
  }
  to {
    width: 20vw;
  }
}
</style>
<style lang="scss">
  *{ margin: 0; padding: 0; border: none; font-size: 2vmin; }
  [v-cloak] {
    display: none;
  }
</style>