<template>
  <div class="note-wrapper">
    <div class="contents">
      <div class="contents-btn" @click="contentsListShow = !contentsListShow" v-clickoutside="handleClose">
        <i class="iconfont icon-list"></i>
      </div>
      <transition name="contents">
        <div class="contents-list" v-show="contentsListShow">
          <div class="list-item" @click="pageSelect('0')">
            <div class="cell">全部</div>
          </div>
          <div class="list-item" @click="pageSelect('2021')">
            <div class="cell">2021年</div>
          </div>
          <div class="list-item" @click="pageSelect('2020')">
            <div class="cell">2020年</div>
          </div>
        </div>
      </transition>
    </div>
    <div class="markdown">
      <note2021 class="markdown-body" v-if="noteSelet === '0' || noteSelet === '2021'" /><br />
      <note2020 class="markdown-body" v-if="noteSelet === '0' || noteSelet === '2020'" />
    </div>
  </div>
</template>

<script>
import note2020 from '@/assets/md/note2020/note.md';
import note2021 from '@/assets/md/note2021/note.md';
export default {
  name: "Note",
  components: {
    note2020,
    note2021
  },
  data() {
    return {
      contentsListShow: false,
      noteSelet: '0'
    }
  },
  directives: {
    // 点击空白事件
    clickoutside: {
      // 初始化指令
      bind(el, binding, vnode) {
        console.log('123')
        function documentHandler(e) {
          // 这里判断点击的元素是否是本身，是本身，则返回
          if (el.contains(e.target)) {
            return false;
          }
        // 判断指令中是否绑定了函数
          if (binding.expression) {
            // 如果绑定了函数 则调用那个函数，此处binding.value就是handleClose方法
            binding.value(e);
          }
        }
      // 给当前元素绑定个私有变量，方便在unbind中可以解除事件监听
        el.__vueClickOutside__ = documentHandler;
        document.addEventListener('click', documentHandler);
      },
      update() {},
      unbind(el, binding) {
        // 解除事件监听
        document.removeEventListener('click', el.__vueClickOutside__);
        delete el.__vueClickOutside__;
      },
    }
  },
  // mounted() {
    // document.addEventListener('click',(e) => {
    //   console.log('e', e);
    //   this.contentsListShow = false
    // }),
  // },
  methods: {
    handleClose(e) {
      this.contentsListShow = false;
    },
    pageSelect(n) {
      this.noteSelet = n;
    }
  },
};

const clickoutside = {
  // 初始化指令
  bind(el, binding, vnode) {
    function documentHandler(e) {
  	  // 这里判断点击的元素是否是本身，是本身，则返回
      if (el.contains(e.target)) {
        return false;
      }
	  // 判断指令中是否绑定了函数
      if (binding.expression) {
  	    // 如果绑定了函数 则调用那个函数，此处binding.value就是handleClose方法
        binding.value(e);
      }
    }
 	// 给当前元素绑定个私有变量，方便在unbind中可以解除事件监听
    el.__vueClickOutside__ = documentHandler;
    document.addEventListener('click', documentHandler);
  },
  update() {},
  unbind(el, binding) {
    // 解除事件监听
    document.removeEventListener('click', el.__vueClickOutside__);
    delete el.__vueClickOutside__;
  },
};
</script>
<style lang="scss" scoped>
  @import '@/assets/css/base.scss';
  .note-wrapper {
    position: relative;
    padding: 2vh 2vw;
    .contents {
      position: fixed;
      right: 4vw;
      top: 4vh;
      width: 5vh;
      height: 5vh;
      .contents-btn {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background: #75BAD2;
        cursor: pointer;
        &:hover {
          background: #8ADBF8;
        }
      }
      .icon-list {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: #fff;
      }
      .contents-list {
        position: absolute;
        top: 150%;
        right: 0;
        width: 12vmin;
        border-radius: 4px;
        overflow: hidden;
        color: #fff;
        background: #75BAD2;
        .list-item {
          padding: 0 1vw;
          text-align: center;
          cursor: pointer;
          .cell {
            padding: 1vh 0;
          }
          &:hover {
            background: #8ADBF8;
          }
          &+.list-item .cell{
            border-top: 1px solid #fff;
          }
        }
      }
    }
    .markdown-body {
      margin-top: 2vh;
    }
  }
  
// 动画&过渡
  .contents-enter-active {
    transition: all 0.5s ease;
  }
  .contents-leave-active {
    transition: all 0.2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .contents-enter, .contents-leave-to
  /* .contents-leave-active for below version 2.1.8 */ {
    transform: translateY(-2vh);
    opacity: 0;
  }

  // @keyframes top-fade-in {
  //   from {
  //     transform: scaleY(-2vh);
  //   }
  //   to {
  //     transform: scaleY(0);
  //   }
  // }
</style>