<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>
export default {
   mounted() {
     console.log('warning')
    if (this._isMobile()) {
      this.$router.push('/m_layout');
    } else {
      // alert("pc端");
      this.$router.push('/');
    }
  },
  methods: {
    _isMobile() {
      let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
      return flag;
    }
  },
}
</script>
